.MainContentForm {
    display: flex;
    align-items: center;
}

input {
    box-sizing: border-box;
    margin: 0;
    background: #FFFFFF;
    width: 300px;
    height: 54px;
    border: 4px solid #000000;
    border-radius: 8px;
    margin-right: 10px;
    outline: none;
    font-weight: 600;
    font-size: 15px;
    padding-left: 10px;
}

button {
    box-sizing: border-box;
    background: #282828;
    color: #FFFFFF;
    margin: 0;
    width: 200px;
    height: 54px;
    border-radius: 8px;
    padding: 15px 5px;
    border: none;
    outline: none;
    font-size: 20px;
    cursor: pointer;
}

#last-text {
    margin-top: 15px;
    color: #FFFFFF;
    margin-left: 27px;
    display: block;
}